import React, { useEffect, useState } from "react";
import { Button, SIZE, SHAPE } from "baseui/button";
import ArrowRight from "baseui/icon/arrow-right";
import { Notification } from "baseui/notification";

import Loading from "../components/Loading";
import Page from "../components/Page";

import { useAppStore } from "../stores";
import { useHistory } from "react-router-dom";

export default function Notifications() {
  const history = useHistory();
  const { api } = useAppStore();
  const [notifications, setNotifications] = useState([]);
  const [loading, setLoading] = useState(true);
  const fecthNotifications = ()=>{
    api
    .get("/notifications")
    .then((res) => {
      setNotifications(res.data.data);
      setLoading(false);
    })
    .catch(() => {
      alert("error");
      setLoading(false);
    });
  }
  useEffect(() => {
    fecthNotifications()
    const interval = setInterval(fecthNotifications, 10000);
    return () => clearInterval(interval);
  }, []);

  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }
  return (
    <Page>
      {notifications.map((n) => (
        <>
          <Notification
            key={n.id}
            overrides={{
              Body: {
                style: {
                  width: "auto",
                  backgroundColor: n.is_read ? "#ffffff" : "#ebebeb",
                  color: n.is_read ? "black" : "#1e54b7",
                },
              },
            }}
          >
            <h2>{n.title}</h2>
            <h3>{n.sub_title}</h3>
            <h4>
              Par: {n?.from?.firstname + " " + n?.from?.lastname?.toUpperCase()}{" "}
              | {n.from.mail}
            </h4>
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              <Button
                size={SIZE.compact}
                shape={SHAPE.pill}
                endEnhancer={() => <ArrowRight size={16} />}
                onClick={() => {
                  history.push(n.url);
                  const data = {
                    is_read: true,
                  };
                  api.put("notifications/" + n.id, data);
                }}
              >
                Plus de detaile
              </Button>
              <div>Le: {n.created_at}</div>
            </div>
          </Notification>
          <hr />
        </>
      ))}
    </Page>
  );
}
