import React, { useEffect, useState } from "react";
import { useRouteMatch, useHistory } from "react-router-dom";
import { Button, SIZE, SHAPE } from "baseui/button";

import Page from "../components/Page";
import { useAppStore } from "../stores";
import BackButton from "../components/BackButton";
import Loading from "../components/Loading";

import { Switch, Route } from "react-router-dom";
import { FormControl } from "baseui/form-control";
import { Input } from "baseui/input";
import { Textarea } from "baseui/textarea";
import { FaEdit, FaEye, FaTrash } from "react-icons/fa";

import { useForm, Controller } from "react-hook-form";
import { Combobox } from "baseui/combobox";
import {
  Checkbox,
  StatefulCheckbox,
  STYLE_TYPE,
  LABEL_PLACEMENT,
} from "baseui/checkbox";

export default function () {
  const { params } = useRouteMatch();
  const { api } = useAppStore();
  const [affectation, setAffectation] = useState();
  const [loading, setLoading] = useState(true);
  const hookForm = useForm();

  const { control, handleSubmit, errors } = hookForm;
  const [carteGrise, setCarteGrise] = useState(false);
  const [assurance, setAssurance] = useState(false);
  const [vignette, setVignette] = useState(false);
  const [carteGpl, setCarteGpl] = useState(false);
  const [permisCirculer, setPermisCirculer] = useState(false);
  const [scanner, setScanner] = useState(false);
  const [gaComment, setGaComment] = useState("");

  const [driverValue, setDriverValue] = React.useState("");
  const [driverOptions, setDriverOptions] = React.useState([]);
  const [driverOption, setDriverOption] = React.useState(null);
  const fetchDriverOptions = (searchTerm, callback) => {
    api.get(`/drivers/search?data=${searchTerm}`).then((res) => {
      callback(res.data.data);
    });
  };

  const onSubmit = () => {
    if (!driverOption) {
      alert("Veullez selectione conducteur!");
      return;
    }
    const data = {};
    data["driver_id"] = driverOption.id;
    data["carte_grise"] = carteGrise;
    data["assurance"] = assurance;
    data["vignette"] = vignette;
    data["carte_gpl"] = carteGpl;
    data["permis_circuler"] = permisCirculer;
    data["scanner"] = scanner;
    data["ga_comment"] = gaComment;
    api
      .post("/ga_checklist/" + params.id, data)
      .then(() => {
        console.log("success");
      })
      .catch(() => {
        console.log("error");
      });
  };
  useEffect(() => {
    api
      .get(`/affectations/${params.id}`)
      .then((res) => {
        setAffectation(res.data.data);
        setLoading(false);
      })
      .catch((err) => {
        alert("error");
        setLoading(false);
      });
  }, []);
  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }
  return (
    <Page>
      <BackButton
        title={` Check List Administratif vhicule: ${affectation?.car?.matricule}`}
      />
      <form style={{ width: 500 }} onSubmit={handleSubmit(onSubmit)}>
        <FormControl label="Conducteur">
          <Combobox
            value={driverValue}
            onChange={(nextValue, option) => {
              setDriverValue(nextValue);
              setDriverOption(option);
              fetchDriverOptions(nextValue, setDriverOptions);
            }}
            mapOptionToString={(option) => {
              return option.firstname + " " + option.lastname;
            }}
            options={driverOptions}
            name="driver_id"
            size="compact"
          />
        </FormControl>
        <FormControl>
          <Checkbox
            checked={carteGrise}
            onChange={(e) => {
              setCarteGrise(e.target.checked);
            }}
            checkmarkType={STYLE_TYPE.toggle}
            labelPlacement={LABEL_PLACEMENT.left}
          >
            Carte grise
          </Checkbox>
        </FormControl>
        <FormControl>
          <Checkbox
            checked={assurance}
            onChange={(e) => {
              setAssurance(e.target.checked);
            }}
            checkmarkType={STYLE_TYPE.toggle}
            labelPlacement={LABEL_PLACEMENT.left}
          >
            Assurance
          </Checkbox>
        </FormControl>
        <FormControl>
          <Checkbox
            checked={vignette}
            onChange={(e) => {
              setVignette(e.target.checked);
            }}
            checkmarkType={STYLE_TYPE.toggle}
            labelPlacement={LABEL_PLACEMENT.left}
          >
            Vignette
          </Checkbox>
        </FormControl>
        <FormControl>
          <Checkbox
            checked={carteGpl}
            onChange={(e) => {
              setCarteGpl(e.target.checked);
            }}
            checkmarkType={STYLE_TYPE.toggle}
            labelPlacement={LABEL_PLACEMENT.left}
          >
            Carte GPL
          </Checkbox>
        </FormControl>
        <FormControl>
          <Checkbox
            checked={permisCirculer}
            onChange={(e) => {
              setPermisCirculer(e.target.checked);
            }}
            checkmarkType={STYLE_TYPE.toggle}
            labelPlacement={LABEL_PLACEMENT.left}
          >
            Permis Circuler
          </Checkbox>
        </FormControl>
        <FormControl>
          <Checkbox
            checked={scanner}
            onChange={(e) => {
              setScanner(e.target.checked);
            }}
            checkmarkType={STYLE_TYPE.toggle}
            labelPlacement={LABEL_PLACEMENT.left}
          >
            Scanner
          </Checkbox>
        </FormControl>
        <FormControl label="Commentaire">
          <Textarea
            value={gaComment}
            onChange={(e) => setGaComment(e.target.value)}
            placeholder=""
            clearOnEscape
          />
        </FormControl>
        <Button
          style={{ width: "100%" }}
          size={SIZE.compact}
          shape={SHAPE.pill}
          type="submit"
        >
          Enregistrer
        </Button>
      </form>
    </Page>
  );
}
