import { Button, SIZE, SHAPE } from "baseui/button";
import { useHistory, useRouteMatch } from "react-router-dom";
import { DatePicker } from "baseui/datepicker";
import fr from "date-fns/locale/fr";
import * as moment from "moment";
import { Combobox } from "baseui/combobox";
import { Select, TYPE } from "baseui/select";
import { _motifs_restitition_client } from "../constants";
import {
  FaEdit,
  FaEye,
  FaTrash,
  FaHistory,
  FaTasks,
  FaBackspace,
  FaExternalLinkAlt,
  FaCartPlus,
  FaLevelDownAlt,
  FaLevelUpAlt,
} from "react-icons/fa";
import { Textarea } from "baseui/textarea";

import { useSnackbar } from "baseui/snackbar";
import {
  Checkbox,
  StatefulCheckbox,
  STYLE_TYPE,
  LABEL_PLACEMENT,
} from "baseui/checkbox";

import { useStyletron } from "baseui";
import Alert from "baseui/icon/alert.js";
import Check from "baseui/icon/check.js";
import ChevronRight from "baseui/icon/chevron-right";

import {
  StatefulDataTable,
  BooleanColumn,
  CategoricalColumn,
  NumericalColumn,
  StringColumn,
  NUMERICAL_FORMATS,
} from "baseui/data-table";

import { useForm, Controller } from "react-hook-form";

import { useAppStore } from "../stores";

import Page from "../components/Page";
import BackButton from "../components/BackButton";
import Loading from "../components/Loading";

export default function Affectations() {
  return (
    <Switch>
      <Route path="/affectations" exact>
        <All />
      </Route>
      <Route path="/affectations/add">
        <Add />
      </Route>
      <Route path="/affectations/edit/:id">
        <Edit />
      </Route>
      <Route path="/affectations/show/:id">
        <Show />
      </Route>
      <Route path="/affectations/restitition/:id">
        <Restitition />
      </Route>
    </Switch>
  );
}

const columns = [
  StringColumn({
    title: "Vhicule",
    mapDataToValue: (data) => data["car"]["matricule"],
  }),
  StringColumn({
    title: "Client",
    mapDataToValue: (data) => data["client"]["designation"],
  }),
  StringColumn({
    title: "Date Affectation",
    mapDataToValue: (data) => data["date_affectation"],
  }),
  StringColumn({
    title: "Date Fin Prstation",
    mapDataToValue: (data) => data["date_end_prestation"] ? data["date_end_prestation"] : "",
  }),
  StringColumn({
    title: "Date Restitition",
    mapDataToValue: (data) => data["date_restitition"] ? data["date_restitition"]:"",
  }),
];

export function All() {
  const [rows, setRows] = React.useState([]);
  const [loading, setLoading] = useState(true);
  const history = useHistory();
  const { api } = useAppStore();
  const { enqueue } = useSnackbar();

  useEffect(() => {
    api
      .get("/affectations")
      .then((res) => {
        setRows(res?.data?.data?.map((r) => ({ id: r.id, data: r })));
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  }, []);

  function removeRows(ids) {
    const nextRows = rows.filter((row) => !ids.includes(row.id));
    setRows(nextRows);
  }
  function removeRow(id) {
    removeRows([id]);
  }
  const batchActions = [
    {
      label: "Exporter",
      onClick: ({ clearSelection }) => clearSelection(),
    },
  ];
  const rowActions = [
    {
      label: "Restitition",
      onClick: ({ row }) => {
        history.push("/affectations/restitition/" + row.data.id);
      },
      renderIcon: ({ size }) => <FaLevelDownAlt size={size} />,
    },
    {
      label: "Affiche",
      onClick: ({ row }) => history.push("/affectations/edit/" + row.id),
      renderIcon: ({ size }) => <FaEdit size={size} />,
    },
    {
      label: "Modifier",
      onClick: ({ row }) => history.push("/affectations/show/" + row.id),
      renderIcon: ({ size }) => <FaEye size={size} />,
    },
    {
      label: "Supprimer",
      onClick: ({ row }) => {
        if (window.confirm("Supprimer ?")) {
          api
            .delete("/affectations/" + row.id)
            .then((res) => {
              removeRow(row.id);
              enqueue({ message: "Success" });
            })
            .catch(() => {
              enqueue({ message: "Error" });
            });
        }
      },
      renderIcon: ({ size }) => <FaTrash size={size} />,
    },
  ];
  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }
  return (
    <Page style={{height: "80vh"}}>
      <Button
        onClick={() => history.push("/affectations/add")}
        size={SIZE.compact}
        shape={SHAPE.pill}
      >
        Nouvelle Affectation
      </Button>
      <hr />
      <StatefulDataTable
        batchActions={batchActions}
        rowActions={rowActions}
        columns={columns}
        rows={rows}
      />
    </Page>
  );
}

function Add() {
  const [roles, setRoles] = useState([]);
  const history = useHistory();
  const [checkedUserRoles, setCheckedUserRoles] = React.useState([]);
  const [loading, setLoading] = useState(false);
  const { control, handleSubmit, errors } = useForm();
  const { api } = useAppStore();
  const { enqueue } = useSnackbar();

  const [carValue, setCarValue] = React.useState("");
  const [carOptions, setCarOptions] = React.useState([]);
  const [carOption, setCarOption] = React.useState(null);
  const fetchCarOptions = (searchTerm, callback) => {
    api.get(`/cars/search?data=${searchTerm}`).then((res) => {
      callback(res.data.data);
    });
  };

  const [clientValue, setClientValue] = React.useState("");
  const [clientOptions, setClientOptions] = React.useState([]);
  const [clientOption, setClientOption] = React.useState(null);
  const fetchClientOptions = (searchTerm, callback) => {
    api.get(`/clients/search?data=${searchTerm}`).then((res) => {
      callback(res.data.data);
    });
  };

  const [driverValue, setDriverValue] = React.useState("");
  const [driverOptions, setDriverOptions] = React.useState([]);
  const [driverOption, setDriverOption] = React.useState(null);
  const fetchDriverOptions = (searchTerm, callback) => {
    api.get(`/drivers/search?data=${searchTerm}`).then((res) => {
      callback(res.data.data);
    });
  };

  const [date_affectation, setDate_affectation] = useState(new Date());
  const [date_end_prestation, setDateEndPrestation] = useState(null);
  const [withDriver, setWithDriver] = useState(false);
  const [carteGrise, setCarteGrise] = useState(false);
  const [assurance, setAssurance] = useState(false);
  const [vignette, setVignette] = useState(false);
  const [carteGpl, setCarteGpl] = useState(false);
  const [permisCirculer, setPermisCirculer] = useState(false);
  const [scanner, setScanner] = useState(false);

  const [roue_secour, setRoue_secour] = useState(false);
  const [poste_radio, setPoste_radio] = useState(false);
  const [cric, setCric] = useState(false);
  const [cle_roue, setCle_roue] = useState(false);
  const [cle_vehicule, setCle_vehicule] = useState(1);

  const [affectation_client_comment, setAffectation_client_comment] = useState(
    ""
  );

  const onSubmit = async (data) => {
    if (!carValue || !clientValue) {
      alert("Il ya des champs vides");
      return;
    }
    data["date_affectation"] = date_affectation;
    data["date_end_prestation"] = date_end_prestation;
    data["car_id"] = carOption?.id;
    data["client_id"] = clientOption?.id;
    data["affectation_client_comment"] = affectation_client_comment;

    api
      .post("/affectations", data)
      .then((res) => {
        enqueue({ message: "Success" });
        history.push("/affectations");
      })
      .catch((err) => {
        alert("Error ! " + err?.response?.data?.message);
      });
  };

  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }

  return (
    <Page>
      <BackButton title="Affect une vhicule" />
      <form style={{ width: 500 }} onSubmit={handleSubmit(onSubmit)}>
        <FormControl label="Vhicule">
          <Combobox
            value={carValue}
            onChange={(nextValue, option) => {
              setCarValue(nextValue);
              setCarOption(option);
              fetchCarOptions(nextValue, setCarOptions);
            }}
            mapOptionToString={(option) => {
              return option.matricule;
            }}
            options={carOptions}
            name="car_id"
            size="compact"
            required
          />
        </FormControl>
        <FormControl label="Client">
          <Combobox
            value={clientValue}
            onChange={(nextValue, option) => {
              setClientValue(nextValue);
              setClientOption(option);
              fetchClientOptions(nextValue, setClientOptions);
            }}
            mapOptionToString={(option) => {
              return option.designation;
            }}
            options={clientOptions}
            name="client_id"
            size="compact"
          />
        </FormControl>

        <FormControl label="Date Affectation">
          <DatePicker
            value={date_affectation}
            onChange={({ date }) => {
              setDate_affectation(date);
              console.log(date);
            }}
            formatString="dd/MM/yyyy"
            name="date_affectation"
            mask={null}
            locale={fr}
            size="compact"
          />
        </FormControl>
        <FormControl label="Date Fin du Prstation">
          <DatePicker
            value={date_end_prestation}
            onChange={({ date }) => {
              setDateEndPrestation(date);
              console.log(date);
            }}
            formatString="dd/MM/yyyy"
            name="date_end_prestation"
            mask={null}
            locale={fr}
            size="compact"
          />
        </FormControl>
        <FormControl label="Commentaire">
          <Textarea
            value={affectation_client_comment}
            onChange={(e) => setAffectation_client_comment(e.target.value)}
            placeholder=""
            clearOnEscape
          />
        </FormControl>

        <hr />

        <Button
          style={{ width: "100%" }}
          type="submit"
          size={SIZE.compact}
          shape={SHAPE.pill}
        >
          Enregistrer
        </Button>
      </form>
    </Page>
  );
}

