function Restitition() {
  const { params } = useRouteMatch();
  const history = useHistory();
  const { api } = useAppStore();
  const [loading, setLoading] = useState(true);
  const [affectation, setAffectation] = useState(null);
  const [date_restitition, setDate_restitition] = useState(new Date());
  const [motif_restitition, setMotif_restitition] = useState("");
  const [restitition_comment, setRestitition_comment] = useState("");

  useEffect(() => {
    api
      .get("affectations/" + params.id)
      .then((res) => {
        console.log(res.data.data);
        setAffectation(res.data.data);
        res.data.data.date_restitition
          ? setDate_restitition(new Date(res.data.data.date_restitition))
          : setDate_restitition(new Date());
        setMotif_restitition([{ id: res.data.data.motif_restitition }]);
        setRestitition_comment(res.data.data.restitition_comment);
        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
        setLoading(false);
      });
  }, []);

  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }

  const onSubmit = (e) => {
    e.preventDefault();
    const data = {};
    data["date_restitition"] = date_restitition;
    data["motif_restitition"] = motif_restitition[0].id;
    data["restitition_comment"] = restitition_comment;

    api
      .post("affectations/restitition/" + params.id, data)
      .then((res) => {
        alert("Success");
        history.goBack();
      })
      .catch((err) => {
        alert(err);
      });
  };

  const onDelete = (e) => {
    e.preventDefault();
    const data = {};
    data["date_restitition"] = null;
    data["motif_restitition"] = null;
    data["restitition_comment"] = null;

    api
      .post("affectations/restitition/" + params.id, data)
      .then((res) => {
        alert("Success");
        history.goBack();
      })
      .catch((err) => {
        alert(err);
      });
  };

  if (affectation?.date_restitition) {
    return (
      <Page>
        <BackButton
          title={
            "Modifier La Restitition De Vhicule: " + affectation.car.matricule
          }
        />
        <form style={{ width: 500 }} onSubmit={onSubmit}>
          <FormControl label="Vhicule">
            <Input
              value={affectation.car.matricule}
              name="car_id"
              size="compact"
            />
          </FormControl>
          <FormControl label="Client">
            <Input
              value={affectation.client.designation}
              name="client_id"
              size="compact"
            />
          </FormControl>

          <FormControl label="Date Affectation">
            <Input value={affectation.date_affectation} size="compact" />
          </FormControl>

          <FormControl label="Date Fin du Prstation">
            <Input
              value={affectation.date_end_prestation}
              disabled={!affectation.date_end_prestation}
              size="compact"
            />
          </FormControl>

          <FormControl label="Date Restitition">
            <DatePicker
              value={date_restitition}
              onChange={({ date }) => {
                setDate_restitition(date);
              }}
              formatString="dd/MM/yyyy"
              name="date_restitition"
              mask={null}
              locale={fr}
              size="compact"
            />
          </FormControl>
          <FormControl label="Motif De Restitition">
            <Select
              value={motif_restitition}
              options={_motifs_restitition_client}
              onChange={(params) => setMotif_restitition(params.value)}
              required
              size="compact"
              placeholder=""
            />
          </FormControl>
          <FormControl label="Commentaire">
            <Textarea
              value={restitition_comment}
              onChange={(e) => setRestitition_comment(e.target.value)}
              placeholder=""
              clearOnEscape
            />
          </FormControl>
          <hr />
          <div
            style={{
              display: "flex",
              width: "100%",
              justifyContent: "space-between",
            }}
          >
            <Button
              style={{ width: "47%" }}
              type="submit"
              size={SIZE.compact}
              shape={SHAPE.pill}
            >
              Enregistrer
            </Button>
            <Button
              style={{ width: "47%", backgroundColor: "#f44336" }}
              onClick={onDelete}
              size={SIZE.compact}
              shape={SHAPE.pill}
            >
              Supprimer la restitition
            </Button>
          </div>
        </form>
      </Page>
    );
  } else {
    return (
      <Page>
        <BackButton
          title={"Restitition de vhicule: " + affectation.car.matricule}
        />
        <form style={{ width: 500 }} onSubmit={onSubmit}>
          <FormControl label="Vhicule">
            <Input
              value={affectation.car.matricule}
              name="car_id"
              size="compact"
            />
          </FormControl>
          <FormControl label="Client">
            <Input
              value={affectation.client.designation}
              name="client_id"
              size="compact"
            />
          </FormControl>

          <FormControl label="Date Affectation">
            <Input value={affectation.date_affectation} size="compact" />
          </FormControl>

          <FormControl label="Date Fin du Prstation">
            <Input
              value={affectation.date_end_prestation}
              disabled={!affectation.date_end_prestation}
              size="compact"
            />
          </FormControl>

          <FormControl label="Date Restitition">
            <DatePicker
              value={date_restitition}
              onChange={({ date }) => {
                setDate_restitition(date);
              }}
              formatString="dd/MM/yyyy"
              name="date_restitition"
              mask={null}
              locale={fr}
              size="compact"
            />
          </FormControl>
          <FormControl label="Motif De Restitition">
            <Select
              value={motif_restitition}
              options={_motifs_restitition_client}
              onChange={(params) => setMotif_restitition(params.value)}
              required
              size="compact"
              placeholder=""
            />
          </FormControl>
          <FormControl label="Commentaire">
            <Textarea
              value={restitition_comment}
              onChange={(e) => setRestitition_comment(e.target.value)}
              placeholder=""
              clearOnEscape
            />
          </FormControl>
          <hr />
          <Button
            style={{ width: "100%" }}
            type="submit"
            size={SIZE.compact}
            shape={SHAPE.pill}
          >
            Enregistrer
          </Button>
        </form>
      </Page>
    );
  }
}
