import React, { useEffect, useState } from "react";
import { Switch, Route } from "react-router-dom";
import { FormControl } from "baseui/form-control";
import { Input } from "baseui/input";
import { Button, SIZE, SHAPE } from "baseui/button";
import { useHistory, useRouteMatch } from "react-router-dom";
import { FaEdit, FaEye, FaTrash } from "react-icons/fa";

import { useSnackbar } from "baseui/snackbar";
import {
  Checkbox,
  StatefulCheckbox,
  STYLE_TYPE,
  LABEL_PLACEMENT,
} from "baseui/checkbox";

import { useStyletron } from "baseui";
import Alert from "baseui/icon/alert.js";
import Check from "baseui/icon/check.js";

import DeleteAlt from "baseui/icon/delete-alt";
import Show from "baseui/icon/show";

import {
  StatefulDataTable,
  BooleanColumn,
  CategoricalColumn,
  NumericalColumn,
  StringColumn,
  NUMERICAL_FORMATS,
} from "baseui/data-table";

import { useForm, Controller } from "react-hook-form";

import { useAppStore } from "../stores";

import Page from "../components/Page";
import BackButton from "../components/BackButton";
import Loading from "../components/Loading";

export default function Users() {
  return (
    <Switch>
      <Route path="/users" exact>
        <All />
      </Route>
      <Route path="/users/add">
        <Add />
      </Route>
      <Route path="/users/edit/:id">
        <Edit />
      </Route>
    </Switch>
  );
}

const columns = [
  StringColumn({
    title: "Nom d'utilisateur",
    mapDataToValue: (data) => data["username"],
  }),
  StringColumn({
    title: "Nom",
    mapDataToValue: (data) => data["firstname"],
  }),
  StringColumn({
    title: "Prenome",
    mapDataToValue: (data) => data["lastname"],
  }),
  StringColumn({
    title: "Email",
    mapDataToValue: (data) => data["mail"],
  }),
  StringColumn({
    title: "Poste",
    mapDataToValue: (data) => data["poste"],
  }),
  StringColumn({
    title: "TEL",
    mapDataToValue: (data) => data["tel"],
  }),
];

export function All() {
  const [rows, setRows] = React.useState([]);
  const [loading, setLoading] = useState(true);
  const history = useHistory();
  const { api } = useAppStore();
  const { enqueue } = useSnackbar();

  useEffect(() => {
    api
      .get("/auth/users")
      .then((res) => {
        setRows(res?.data?.data?.map((r) => ({ id: r.id, data: r })));
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  }, []);

  function removeRows(ids) {
    const nextRows = rows.filter((row) => !ids.includes(row.id));
    setRows(nextRows);
  }
  function removeRow(id) {
    removeRows([id]);
  }
  const batchActions = [
    {
      label: "Exporter",
      onClick: ({ clearSelection }) => clearSelection(),
    },
  ];
  const rowActions = [
    {
      label: "Affiche",
      onClick: ({ row }) => history.push("/users/edit/" + row.id),
      renderIcon: ({ size }) => <FaEdit size={size} />,
    },
    {
      label: "Supprimer",
      onClick: ({ row }) => {
        if (window.confirm("Supprimer ce utilisateur ?")) {
          api
            .delete("/auth/users/" + row.id)
            .then((res) => {
              removeRow(row.id);
              enqueue({ message: "Success" });
            })
            .catch(() => {
              enqueue({ message: "Error" });
            });
        }
      },
      renderIcon: ({ size }) => <FaTrash size={size} />,
    },
  ];
  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }
  return (
    <Page style={{height: "80vh"}}>
      <Button
        onClick={() => history.push("/users/add")}
        size={SIZE.compact}
        shape={SHAPE.pill}
      >
        Ajouter un utilisateur
      </Button>
      <hr />
      <StatefulDataTable
        batchActions={batchActions}
        rowActions={rowActions}
        columns={columns}
        rows={rows}
      />
    </Page>
  );
}

function Add() {
  const [roles, setRoles] = useState([]);
  const history = useHistory();
  const [checkedUserRoles, setCheckedUserRoles] = React.useState([]);
  const [loading, setLoading] = useState(true);
  const { control, handleSubmit, errors } = useForm();
  const { api } = useAppStore();
  const { enqueue } = useSnackbar();

  const onSubmit = async (data) => {
    const rolesTab = [...checkedUserRoles];
    data["roles"] = rolesTab;
    const res = await api.post("/auth/users", data);
    if (res) {
      enqueue({ message: "utilisateur cr" });
      history.push("/users/edit/" + res?.data?.data?.id + "");
    } else {
      enqueue({ message: "Error !" });
    }
  };

  useEffect(() => {
    api
      .get("/roles")
      .then(({ data }) => {
        const allRoles = data.data;
        const checkedRoles = allRoles.map((role) => ({
          ...role,
          checked: true,
        }));
        setRoles(checkedRoles);
        setLoading(false);
      })
      .catch((err) => {
        const message = err?.response?.data?.message || "" + err;
        enqueue({ message });
        setLoading(false);
      });
  }, []);

  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }

  return (
    <Page>
      <BackButton title="Ajouter un utilisateur" />
      <form style={{ width: 500 }} onSubmit={handleSubmit(onSubmit)}>
        <FormControl
          label="Nom d'utilisateur"
          caption={errors.name === "username" ? "Verifier votre donnee" : null}
          error={errors.name === "username"}
        >
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="username"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Nom">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="firstname"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Prenom">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="lastname"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Email">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="mail"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Mot de pass">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="password"
            type="password"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Poste">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="poste"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="TEL">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="tel"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <div
          style={{
            border: "1px solid lightgray",
            padding: 10,
            marginBottom: 15,
          }}
        >
          <h3>Les permissions pour cet utilisateur:</h3>
          {roles.map((role) => (
            <FormControl key={role.id}>
              <StatefulCheckbox
                onChange={(e) => {
                  console.log(e.target.checked);
                  if (e.target.checked) {
                    checkedUserRoles.push(role.id);
                  } else {
                    const index = checkedUserRoles.indexOf(role.id);
                    checkedUserRoles.splice(index, 1);
                    //setChecked(checked.splice(checked.indexOf(role.id), 1));
                  }
                  console.log(checkedUserRoles);
                }}
                name={role.name}
                checkmarkType={STYLE_TYPE.toggle}
                labelPlacement={LABEL_PLACEMENT.left}
              >
                # {role.name}
              </StatefulCheckbox>
            </FormControl>
          ))}
        </div>
        <Button
          style={{ width: "100%" }}
          type="submit"
          size={SIZE.compact}
          shape={SHAPE.pill}
        >
          Enregistrer
        </Button>
      </form>
    </Page>
  );
}

function Edit() {
  const [loading, setLoading] = useState(true);
  const { enqueue } = useSnackbar();
  const { params } = useRouteMatch();
  const { api } = useAppStore();
  const [selectedUser, setSelectedUser] = useState(null);
  const [roles, setRoles] = useState([]);

  const hookForm = useForm();
  const { control, handleSubmit, errors } = hookForm;

  const onSubmit = async (data) => {
    const roleIDs = [];
    roles.map((r) => {
      if (r.checked) {
        roleIDs.push(r.id);
      }
    });
    data["roles"] = roleIDs;
    const res = await api.put("/auth/users/" + params.id, data);
    if (res) {
      enqueue({ message: "utilisateur est modifier" });
    } else {
      enqueue({ message: "Error !" });
    }
  };

  useEffect(async () => {
    const resUser = await api.get("/auth/users/" + params.id);
    if (resUser) {
      setSelectedUser(resUser.data.data);
    } else {
    }
    const resRoles = await api.get("/roles/");
    if (resRoles) {
      const rolesData = resRoles.data.data;
      const allRoles = rolesData.map((role) => {
        let checked = false;
        for (let i = 0; i < resUser.data.data.roles.length; i++) {
          if (role.name === resUser.data.data.roles[i].name) {
            checked = true;
          }
        }
        return { ...role, checked };
      });
      setRoles(allRoles);
    } else {
    }
    setLoading(false);
  }, []);

  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }

  return (
    <Page>
      <BackButton
        title={` Modifier l'utilisateur: ${selectedUser?.username}`}
      />
      <form style={{ width: 500 }} onSubmit={handleSubmit(onSubmit)}>
        <FormControl
          label="Nom d'utilisateur"
          caption={errors.name === "username" ? "Verifier votre donnee" : null}
          error={errors.name === "username"}
        >
          <Controller
            control={control}
            defaultValue={selectedUser?.username}
            as={Input}
            required
            name="username"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Nom">
          <Controller
            control={control}
            defaultValue={selectedUser?.firstname}
            as={Input}
            required
            name="firstname"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Prenom">
          <Controller
            control={control}
            defaultValue={selectedUser?.lastname}
            as={Input}
            required
            name="lastname"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Mail">
          <Controller
            control={control}
            defaultValue={selectedUser?.mail}
            as={Input}
            required
            name="mail"
            size="compact"
            placeholder=""
          />
        </FormControl>

        <FormControl label="Mot de pass">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            name="password"
            type="password"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Poste">
          <Controller
            control={control}
            defaultValue={selectedUser?.poste}
            as={Input}
            required
            name="poste"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="TEL">
          <Controller
            control={control}
            defaultValue={selectedUser?.tel}
            as={Input}
            required
            name="tel"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <div
          style={{
            border: "1px solid lightgray",
            padding: 10,
            marginBottom: 15,
          }}
        >
          <h3>Les permissions pour ce utilisateur:</h3>
          {roles.map((role) => (
            <FormControl key={role.id}>
              <Checkbox
                checked={role.checked}
                onChange={(e) => {
                  const newRoles = roles.map((r) => {
                    if (r.id === role.id) {
                      return {
                        ...r,
                        checked: e.target.checked,
                      };
                    }
                    return r;
                  });
                  setRoles(newRoles);
                }}
                checkmarkType={STYLE_TYPE.toggle}
                labelPlacement={LABEL_PLACEMENT.left}
              >
                # {role.name}
              </Checkbox>
            </FormControl>
          ))}
        </div>
        <Button
          style={{ width: "100%" }}
          size={SIZE.compact}
          shape={SHAPE.pill}
          type="submit"
        >
          Enregistrer
        </Button>
      </form>
    </Page>
  );
}
