import React, { useEffect, useState } from "react";
import { Navigation } from "baseui/side-navigation";
import { styled } from "baseui";

import { useAppStore } from "../stores";
import { useHistory, useLocation, useRouteMatch } from "react-router-dom";

const nav = [
  {
    title: "Utiisateurs",
    itemId: "/users",
  },
  {
    title: "Vhicules",
    itemId: "/cars",
  },
  {
    title: "Conducteurs",
    itemId: "/drivers",
  },
  {
    title: "Clients",
    itemId: "/clients",
  },
];

const Container = styled("div", {
  minWidth: "280px",
  borderRight: "1px solid lightgray",
  position: "fixed",
  height: "100vh",
});

export default () => {
  const history = useHistory();
  //const [location, setLocation] = useState("");
  const location = useLocation();
  const [navItems, setNavItems] = useState([]);
  const { user } = useAppStore();

  const hasRole = (r) => {
    let has = false;
    user.roles.map((role) => {
      if (role.name === r) {
        has = true;
      }
    });
    return has;
  };

  useEffect(() => {
    const items = [];
    items.push({
      title: "Notifications",
      itemId: "/notifications",
    });
    items.push({
      title: "Etat Des Vhicules",
      itemId: "/emr",
    });
    items.push({
      title: "Etat Des Affectations Clients",
      itemId: "/etat_affectations",
    });
    items.push({
      title: "Gestion Des Status Vhicules",
      itemId: "/cars_state",
    });
    if (hasRole("ADD_EDIT_AFFECTATIONS_CLIENTS")) {
      items.push({
        title: "Affectations Clients",
        itemId: "/affectations",
      });
    }

    if (hasRole("ADD_EDIT_DRIVERS")) {
      items.push({
        title: "Dcharges Conducteurs",
        itemId: "/decharges",
      });
    }
    if (hasRole("ADD_EDIT_CLIENTS")) {
      items.push({
        title: "Gestion Des Clients",
        itemId: "/clients",
      });
    }
    if (hasRole("ADD_EDIT_DRIVERS")) {
      items.push({
        title: "Gestion Des Conducteurs",
        itemId: "/drivers",
      });
    }
    if (hasRole("ADD_EDIT_CARS")) {
      items.push({
        title: "Gestion Des Vhicules",
        itemId: "/cars",
      });
    }
    if (hasRole("ADD_EDIT_USERS")) {
      items.push({
        title: "Gestion Des Utilisateurs",
        itemId: "/users",
      });
    }

    setNavItems(items);
  }, []);
  return (
    <Container>
      <Navigation
        items={navItems}
        activePredicate={(item) => {
          return (location?.pathname?.split("/")[1]) === (item.itemId?.split("/")[1]);
        }}
        onChange={({ event, item }) => {
          event.preventDefault();
          history.push(item.itemId);
          //      setLocation(item.itemId);
        }}
      />
    </Container>
  );
};
