import React, { useEffect, useState } from "react";
import { Switch, Route } from "react-router-dom";
import { FormControl } from "baseui/form-control";
import { Input } from "baseui/input";
import { Button, SIZE, SHAPE } from "baseui/button";
import { useHistory, useRouteMatch } from "react-router-dom";
import { FaEdit, FaEye, FaTrash } from "react-icons/fa";
import { Combobox } from "baseui/combobox";
import { useSnackbar } from "baseui/snackbar";
import {
  Checkbox,
  StatefulCheckbox,
  STYLE_TYPE,
  LABEL_PLACEMENT,
} from "baseui/checkbox";

import { useStyletron } from "baseui";
import Alert from "baseui/icon/alert.js";
import Check from "baseui/icon/check.js";

import DeleteAlt from "baseui/icon/delete-alt";
import Show from "baseui/icon/show";

import {
  StatefulDataTable,
  BooleanColumn,
  CategoricalColumn,
  NumericalColumn,
  StringColumn,
  NUMERICAL_FORMATS,
} from "baseui/data-table";

import { useForm, Controller } from "react-hook-form";

import { useAppStore } from "../stores";

import Page from "../components/Page";
import BackButton from "../components/BackButton";
import Loading from "../components/Loading";

export default function Cars() {
  return (
    <Switch>
      <Route path="/clients" exact>
        <All />
      </Route>
      <Route path="/clients/add">
        <Add />
      </Route>
      <Route path="/clients/edit/:id">
        <Edit />
      </Route>
    </Switch>
  );
}

const columns = [
  StringColumn({
    title: "Code(SAP)",
    mapDataToValue: (data) => data["code"],
  }),
  StringColumn({
    title: "Dsignation",
    mapDataToValue: (data) => data["designation"],
  }),
  StringColumn({
    title: "Client Hirarchique",
    mapDataToValue: (data) =>
      data["client"] ? data["client"]["designation"] : "-",
  }),
];

export function All() {
  const [rows, setRows] = React.useState([]);
  const [loading, setLoading] = useState(true);
  const history = useHistory();
  const { api } = useAppStore();
  const { enqueue } = useSnackbar();

  useEffect(() => {
    api
      .get("/clients")
      .then((res) => {
        setRows(res?.data?.data?.map((r) => ({ id: r.id, data: r })));
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  }, []);

  function removeRows(ids) {
    const nextRows = rows.filter((row) => !ids.includes(row.id));
    setRows(nextRows);
  }
  function removeRow(id) {
    removeRows([id]);
  }
  const batchActions = [
    {
      label: "Exporter",
      onClick: ({ clearSelection }) => clearSelection(),
    },
  ];
  const rowActions = [
    {
      label: "Affiche",
      onClick: ({ row }) => history.push("/clients/edit/" + row.id),
      renderIcon: ({ size }) => <FaEdit size={size} />,
    },
    {
      label: "Supprimer",
      onClick: ({ row }) => {
        if (window.confirm("Supprimer ?")) {
          api
            .delete("/clients/" + row.id)
            .then((res) => {
              removeRow(row.id);
              enqueue({ message: "Success" });
            })
            .catch(() => {
              enqueue({ message: "Error" });
            });
        }
      },
      renderIcon: ({ size }) => <FaTrash size={size} />,
    },
  ];
  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }
  return (
    <Page style={{height: "75vh"}}>
      <Button
        onClick={() => history.push("/clients/add")}
        size={SIZE.compact}
        shape={SHAPE.pill}
      >
        Ajouter un Client/Structure
      </Button>
      <hr />
      <StatefulDataTable
        batchActions={batchActions}
        rowActions={rowActions}
        columns={columns}
        rows={rows}
      />
    </Page>
  );
}

function Add() {
  const [roles, setRoles] = useState([]);
  const history = useHistory();
  const [checkedUserRoles, setCheckedUserRoles] = React.useState([]);
  const [loading, setLoading] = useState(false);
  const { control, handleSubmit, errors } = useForm();
  const { api } = useAppStore();
  const { enqueue } = useSnackbar();

  const [clientValue, setClientValue] = React.useState("");
  const [clientOptions, setClientOptions] = React.useState([]);
  const [clientOption, setClientOption] = React.useState(null);
  const fetchClientOptions = (searchTerm, callback) => {
    api.get(`/clients/search?data=${searchTerm}`).then((res) => {
      callback(res.data.data);
    });
  };
  const onSubmit = async (data) => {
    data["client_id"] = clientOption?.id;
    try {
      const res = await api.post("/clients", data);
      enqueue({ message: "Success" });
      history.push("/clients");
    } catch (error) {
      enqueue({ message: "Error !" });
    }
  };

  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }

  return (
    <Page>
      <BackButton title=" Ajouter un Client/Structure" />
      <form style={{ width: 500 }} onSubmit={handleSubmit(onSubmit)}>
        
        <FormControl label="Code(SAP)">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="code"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Dsignation">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="designation"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Client Hirarchique">
          <Combobox
            value={clientValue}
            onChange={(nextValue, option) => {
              setClientValue(nextValue);
              setClientOption(option);
              fetchClientOptions(nextValue, setClientOptions);
            }}
            mapOptionToString={(option) => {
              return option.designation;
            }}
            options={clientOptions}
            name="client_id"
            size="compact"
          />
        </FormControl>
        <Button
          style={{ width: "100%" }}
          type="submit"
          size={SIZE.compact}
          shape={SHAPE.pill}
        >
          Enregistrer
        </Button>
      </form>
    </Page>
  );
}

function Edit() {
  const [loading, setLoading] = useState(true);
  const { enqueue } = useSnackbar();
  const { params } = useRouteMatch();
  const { api } = useAppStore();
  const [selected, setSelected] = useState(null);
  const history = useHistory();
  const hookForm = useForm();
  const { control, handleSubmit, errors } = hookForm;
  const [clientValue, setClientValue] = React.useState("");
  const [clientOptions, setClientOptions] = React.useState([]);
  const [clientOption, setClientOption] = React.useState(null);
  const fetchClientOptions = (searchTerm, callback) => {
    api.get(`/clients/search?data=${searchTerm}`).then((res) => {
      callback(res.data.data);
    });
  };
  const onSubmit = async (data) => {
    data["client_id"] = clientOption?.id;
    const res = await api.put("/clients/" + params.id, data);
    if (res) {
      enqueue({ message: "Success" });
      history.push("/clients");
    } else {
      enqueue({ message: "Error !" });
    }
  };

  useEffect(async () => {
    const res = await api.get("/clients/" + params.id);
    if (res) {
      setSelected(res.data.data);
      setClientOption(res.data.data?.client);
      setClientValue(res.data.data?.client?.designation);
    } else {
    }
    setLoading(false);
  }, []);

  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }

  return (
    <Page>
      <BackButton title={` Modifier la structure: ${selected?.designation}`} />
      <form style={{ width: 500 }} onSubmit={handleSubmit(onSubmit)}>
        <FormControl label="Code(SAP)">
          <Controller
            control={control}
            defaultValue={selected.code}
            as={Input}
            required
            name="code"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Dsingation">
          <Controller
            control={control}
            defaultValue={selected.designation}
            as={Input}
            required
            name="designation"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Client Hirarchique">
          <Combobox
            value={clientValue}
            onChange={(nextValue, option) => {
              setClientValue(nextValue);
              setClientOption(option);
              fetchClientOptions(nextValue, setClientOptions);
            }}
            mapOptionToString={(option) => {
              return option.designation;
            }}
            options={clientOptions}
            name="client_id"
            size="compact"
          />
        </FormControl>
        <Button
          style={{ width: "100%" }}
          size={SIZE.compact}
          shape={SHAPE.pill}
          type="submit"
        >
          Enregistrer
        </Button>
      </form>
    </Page>
  );
}
