import React, { useState } from "react"
import Page from "../components/Page"
import axios from "axios"
import { useAppStore } from "../stores"
import * as moment from "moment"
import FileDownload from "js-file-download"
export default function Settings() {
    const [file, setFile] = useState(null)
    const { api } = useAppStore()
    window.api = api
    const onFileUpload = () => {
        // Create an object of formData 
        const formData = new FormData();
        // Update the formData object 
        formData.append(
            "cars",
            file,
            file.name
        );

        // Details of the uploaded file 
        console.log(file);

        // Request made to the backend api 
        // Send formData object 
        api.post("/cars/import", formData);
    };

    return (
        <Page title="Parametres">
            <div>
                <h2>Cars</h2>
                <input type="file" onChange={(e) => setFile(e.target.files[0])} />
                <button onClick={onFileUpload}>import file</button>
                <button onClick={() => {
                    api.get("/cars/export", { responseType: 'blob' }).then((res) => {
                        FileDownload(res.data, "cars-" + moment(new Date()).format("D-M-yy") + ".xlsx")
                    })
                }}>export file</button>
            </div>

            <div>
                <h2>Drivers</h2>
                <input type="file" onChange={(e) => setFile(e.target.files[0])} />
                <button onClick={() => {
                    // Create an object of formData 
                    const formData = new FormData();
                    // Update the formData object 
                    formData.append(
                        "drivers",
                        file,
                        file.name
                    );

                    // Details of the uploaded file 
                    console.log(file);

                    // Request made to the backend api 
                    // Send formData object 
                    api.post("/drivers/import", formData);
                }}>import file</button>
                <button onClick={() => {
                    api.get("/drivers/export", { responseType: 'blob' }).then((res) => {
                        FileDownload(res.data, "drivers-" + moment(new Date()).format("D-M-yy") + ".xlsx")
                    })
                }}>export file</button>
            </div>

            <div>
                <h2>Clients</h2>
                <input type="file" onChange={(e) => setFile(e.target.files[0])} />
                <button onClick={() => {
                    // Create an object of formData 
                    const formData = new FormData();
                    // Update the formData object 
                    formData.append(
                        "clients",
                        file,
                        file.name
                    );

                    // Details of the uploaded file 
                    console.log(file);

                    // Request made to the backend api 
                    // Send formData object 
                    api.post("/clients/import", formData);
                }}>import file</button>
                <button onClick={() => {
                    api.get("/drivers/export", { responseType: 'blob' }).then((res) => {
                        FileDownload(res.data, "clients-" + moment(new Date()).format("D-M-yy") + ".xlsx")
                    })
                }}>export file</button>
            </div>
        </Page >
    )
}