import React, { useEffect, useState } from "react";
import Loading from "../components/Loading";
import Page from "../components/Page";
import { useAppStore } from "../stores";

export default function () {
  const { api } = useAppStore();
  const [loading, setLoading] = useState(true);

  const [data, setData] = useState([]);
  useEffect(() => {
    api
      .get("/states")
      .then((res) => {
        setData(res.data.data);
        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
        setLoading(false);
      });
  }, []);
  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }
  return (
    <Page>
      <h2>Vhicules:</h2>
      <div style={{ display: "flex", flexWrap: "wrap" }}>
        <State title="Nbs Vhicules" nb={data?.nbCars}></State>
        <State title="Vhicules Affects" nb={data?.nbCarsAffecte}></State>
        <State
          title="Vhicules Non Affects"
          nb={data?.nbCarsNonAffecte}
        ></State>
        <State title="Vhicules En Panne" nb={data?.nbCarsEntreAtelie}></State>
      </div>
      <hr />
      <h2>Clients:</h2>
      <div style={{ display: "flex", flexWrap: "wrap" }}>
        <State title="Nbs Des Clients" nb={data?.nbClients}></State>
        <State title="Nbs Affectations ce mois" nb={data?.nbAff}></State>
        <State title="Nbs Restititions ce mois" nb={data?.nbRes}></State>
      </div>
      <hr />
      <h2>Conducteurs:</h2>
      <div style={{ display: "flex", flexWrap: "wrap" }}>
        <State title="Nbs Des Conducteurs" nb={data?.nbDrivers}></State>
        <State title="Nbs Dcharges ce mois" nb={data?.nbDech}></State>
        <State title="Nbs Restititions ce mois" nb={data?.nbRDech}></State>
      </div>
    </Page>
  );
}

const State = ({ title, nb }) => (
  <div
    style={{
      cursor: "pointer",
      position: "relative",
      width: 290,
      height: 50,
      margin: 10,
      padding: 10,
      paddingBottom: 50,
      color: "#1e54b7",
      display: "flex",
      flexDirection: "column",
      border: "2px dashed lightgray",
      justifyContent: "center",
      alignItems: "center",
      backgroundColor: "ghostwhite",
      fontSize: 20,
    }}
  >
    <div
      style={{
        position: "absolute",
        right: 5,
        bottom: 5,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: 39,
        width: 39,
        borderRadius: 40,
        backgroundColor: "#1e54b7",
        color: "white",
      }}
    >
      {" "}
      {nb}
    </div>
    <div style={{ fontWeight: "bold" }}>{title}</div>
  </div>
);
