import React from "react";
import ReactToPrint from "react-to-print";

import moment from "moment";

const styles = {
  hide: { display: "none" },
  page: {
    display: "flex",
    flexDirection: "column",
    width: "210mm",
    height: "297mm",
    fontSize: "4mm",
    backgroundColor: "white",
    margin: 0,
    padding: 0,
    border: "none",
    color: "black",
  },
  pageHeader: {
    display: "flex",
    justifyContent: "space-between",
    fontWeight: "bold",
    backgroundColor: "white",
    border: ".15mm solid black",
    height: "25mm",
    margin: "5mm",
  },
  pageBody: {
    display: "flex",
    flex: 1,
    backgroundColor: "white",
    padding: "5mm",
    paddingTop: "1mm",
    paddingBottom: "1mm",
    flexDirection: "column",
  },
  pageFooter: {
    display: "flex",
    flexDirection: "column",
    margin: "5mm",
    marginTop: "1mm",
    fontSize: "3.3mm",
    textAlign: "center",
  },
};

export default class PrintDecharge extends React.Component {
  render() {
    const { decharge } = this.props;
    return (
      <div style={styles.page}>
        <div style={styles.pageHeader}>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              border: ".15mm solid black",
              width: "50mm",
            }}
          >
            <img style={{ width: "45mm" }} src={"/logo.png"} />
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              flex: 1,
              justifyContent: "center",
            }}
          >
            <div
              style={{
                fontSize: "5.5mm",
                textAlign: "center",
                fontWeight: "bold",
                border: ".15mm solid black",
              }}
            >
              SPA CONDOR LOGISTICS
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "space-around",
                alignItems: "center",
                flex: 1,
                textAlign: "center",
                border: ".15mm solid black",
                fontSize: "5mm",
              }}
            >
              <div>DECHARGE</div>
              <div>{`N${decharge.id} DL/${moment().year()}`}</div>
            </div>
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-around",
              textAlign: "center",
              alignItems: "center",
              border: ".15mm solid black",
              width: "50mm",
            }}
          >
            <div style={{ fontSize: "6mm" }}>Version 01</div>
            
          </div>
        </div>
        <div style={styles.pageBody}>
          <div style={{ fontSize: "6mm", marginBottom: "2mm" }}>
            <b>Client: </b>{" "}
            {decharge?.car?.latestClient
              ? decharge?.car?.latestClient?.designation
              : "/"}
            .
          </div>
          <div
            style={{
              fontSize: "5mm",
              border: ".15mm solid black",
              padding: "2mm",
            }}
          >
            <u>
              <b style={{ fontSize: "6mm" }}>Vhicule: </b>
            </u>
            <ul>
              <li>
                <b>Marque:</b> {decharge.car.marque}
              </li>
              <li>
                <b>Matricule:</b> {decharge.car.matricule}
              </li>
              <li>
                <b>Code GPS:</b> {decharge.car.code_gps}
              </li>
              <li>
                <b>Odometre:</b> {decharge.km}
              </li>
              <li>
                <b>Nbs Cls de Vhicule:</b> {decharge.cle_vehicule}
              </li>
            </ul>
          </div>
          <div
            style={{
              marginTop: "2mm",
              fontSize: "5mm",
              border: ".15mm solid black",
              padding: "2mm",
            }}
          >
            <u>Je Soussign Monsieur:</u>{" "}
            <b>
              {" "}
              {decharge.driver.firstname.toUpperCase() +
                " " +
                decharge.driver.lastname.toUpperCase()}
            </b>
            , <u>N TEL:</u> <b>{decharge.driver.tel}</b>
            <br />
            Reconnais avoir reu ce jour{" "}
            <b>{moment(decharge.date_affectation_driver).format("D-M-yy")}</b>:
            <div
              style={{
                marginTop: "4mm",
                fontSize: "4.5mm",
                fontWeight: "bold",
                marginLeft: "5mm",
              }}
            >
              I - <u>Les documents ci-aprs:</u>
            </div>
            <ol style={{ marginLeft: "5mm" }}>
              {" "}
              <Document
                decharge={decharge}
                name="carte_grise"
                title="Carte grise"
              />
              <Document
                decharge={decharge}
                name="assurance"
                title="Assurance"
              />
              <Document decharge={decharge} name="vignette" title="Vignette" />
              <Document
                decharge={decharge}
                name="carte_gpl"
                title="carte GPL"
              />
              <Document
                decharge={decharge}
                name="permis_circuler"
                title="Permis circuler"
              />
              <Document decharge={decharge} name="scanner" title="Scanner" />
            </ol>
            <div
              style={{
                marginTop: "4.5mm",
                fontWeight: "bold",
                marginLeft: "5mm",
              }}
            >
              II - <u>Les equipments ci-aprs:</u>
            </div>
            <ol style={{ marginLeft: "5mm" }}>
              <Document
                decharge={decharge}
                name="roue_secour"
                title="Roue de secours"
              />
              <Document decharge={decharge} name="cric" title="Cric" />
              <Document
                decharge={decharge}
                name="cle_roue"
                title="Cle de roue"
              />
              <Document
                decharge={decharge}
                name="poste_radio"
                title="Poste radio"
              />
            </ol>
          </div>

          <div
            style={{
              border: ".2mm solid black",
              marginTop: "2mm",
              padding: "2mm",
              flex: 1,
            }}
          >
            <u>
              <b>Observations: </b>
            </u>{" "}
            {decharge.affectation_comment}
          </div>
        </div>

        <div style={styles.pageFooter}>
          <div
            style={{
              display: "flex",
              flex: 1,
              height: "10mm",
              border: ".15mm solid black",
            }}
          >
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                flex: 1,
                border: ".15mm solid black",
              }}
            >
              <b>Le Gestionnaire Administrative Matriel Roulant</b>
            </div>
            <div
              style={{
                display: "flex",
                flex: 1,
                justifyContent: "center",
                alignItems: "center",
                border: ".15mm solid black",
              }}
            >
              <b>Le Gestionnaire Matriel Roulant</b>
            </div>
            <div
              style={{
                display: "flex",
                flex: 1,
                justifyContent: "center",
                alignItems: "center",
                border: ".15mm solid black",
              }}
            >
              <b>Le Bnficiare</b>
            </div>
          </div>
          <div style={{ display: "flex", flex: 1, minHeight: "25mm" }}>
            <div
              style={{ display: "flex", flex: 1, border: ".15mm solid black" }}
            ></div>
            <div
              style={{ display: "flex", flex: 1, border: ".15mm solid black" }}
            ></div>
            <div
              style={{ display: "flex", flex: 1, border: ".15mm solid black" }}
            ></div>
          </div>
          <div style={{ border: ".15mm solid black", padding: "1mm" }}>
            {"username: " + decharge?.createdby?.username} |{" "}
            {`Imprimer Le: ${moment().format("D-M-yy")}`} |{" "}
            {document.location.toString()}
          </div>
        </div>
      </div>
    );
  }
}

const Document = ({ decharge, name, title }) => {
  if (decharge[name]) {
    return (
      <li style={{ margin: "2mm" }}>
        <input
          style={{ marginRight: "2mm" }}
          checked={decharge[name]}
          type="checkbox"
        />
        <b>{title}</b>
      </li>
    );
  } else {
    return <></>;
  }
};
