import React, { useEffect } from "react";

export default function ({ style, children, title }) {
  useEffect(() => {
    title ? document.title = title + " - iFlotte v1.0.0" : document.title = "iFlotte v1.0.0"
  }, [title])
  return (
    <div
      style={{
        padding: 20,
        height: "100%",
        flex: 1,
        paddingLeft: 300,
        ...style
      }}
    >
      {children}
    </div>
  );
}
