import React, { useEffect, useState } from "react";
import { Switch, Route } from "react-router-dom";
import { Input } from "baseui/input";
import { Select, TYPE } from "baseui/select";
import { Button, SIZE, SHAPE } from "baseui/button";
import * as moment from "moment";
import { useHistory, useRouteMatch } from "react-router-dom";
import {
  FaEdit,
  FaEye,
  FaTrash,
  FaHistory,
  FaTasks,
  FaBackspace,
  FaExternalLinkAlt,
  FaCartPlus,
  FaLevelDownAlt,
  FaLevelUpAlt,
  FaPeace,
  FaUserTie,
} from "react-icons/fa";
import fr from "date-fns/locale/fr";

import { useSnackbar } from "baseui/snackbar";
import {
  Checkbox,
  StatefulCheckbox,
  STYLE_TYPE,
  LABEL_PLACEMENT,
} from "baseui/checkbox";

import { FormControl } from "baseui/form-control";
import { DatePicker } from "baseui/datepicker";

import { useStyletron } from "baseui";
import Alert from "baseui/icon/alert.js";
import Check from "baseui/icon/check.js";

import DeleteAlt from "baseui/icon/delete-alt";

import {
  StatefulDataTable,
  BooleanColumn,
  CategoricalColumn,
  NumericalColumn,
  StringColumn,
  NUMERICAL_FORMATS,
} from "baseui/data-table";

import { useForm, Controller } from "react-hook-form";

import { useAppStore } from "../stores";

import Page from "../components/Page";
import BackButton from "../components/BackButton";
import Loading from "../components/Loading";

window.moment = moment;

export default function Cars() {
  return (
    <Switch>
      <Route path="/etat_affectations" exact>
        <All />
      </Route>
    </Switch>
  );
}

export function All() {
  const [rows, setRows] = React.useState([]);
  const [loading, setLoading] = useState(false);
  const history = useHistory();
  const { api, user } = useAppStore();
  const { enqueue } = useSnackbar();
  const [dateRange, setDateRange] = useState([
    moment().startOf("month").toDate(),
    moment().endOf("month").toDate(),
  ]);

  useEffect(()=>fetchAffectation(), [])
  const columns = [
    StringColumn({
      title: "Vhicule",
      mapDataToValue: (data) => data["car"]["matricule"],
    }),
    CategoricalColumn({
      title: "Client",
      mapDataToValue: (data) => data["client"]["designation"],
    }),
    StringColumn({
      title: "Date Affectation",
      mapDataToValue: (data) => data["date_affectation"],
    }),
    StringColumn({
      title: "Date Fin Prstation",
      mapDataToValue: (data) =>
        data["date_end_prestation"] ? data["date_end_prestation"] : "-",
    }),
    StringColumn({
      title: "Date Restitition",
      mapDataToValue: (data) =>
        data["date_restitition"] ? data["date_restitition"] : "-",
    }),
    StringColumn({
      title: "Nbs Jour(s)",
      mapDataToValue: (data) => {
        let date1 = moment(data["date_affectation"]).isBefore(
          moment(dateRange[0])
        )
          ? moment(dateRange[0])
          : moment(data["date_affectation"]);

        let date2 = moment(dateRange[1]);
        if (data["date_restitition"]) {
          date2 = moment(data["date_restitition"]).isAfter(date2)
            ? date2
            : moment(data["date_restitition"]);
        }
        const s = date2.diff(date1, "days") + 1
        return s.toString();
      },
    }),
  ];
  const fetchAffectation = () => {
    if (dateRange.length < 2) {
      alert("selection une intervale de deux dates");
      return;
    }
    setLoading(true);
    const date1 = moment(dateRange[0]).format("yy-M-D");
    const date2 = moment(dateRange[1]).format("yy-M-D");

    api
      .get(`/affectations?date1=${date1}&date2=${date2}`)
      .then((res) => {
        setRows(res?.data?.data?.map((r) => ({ id: r.id, data: r })));
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  };

  const batchActions = [
    {
      label: "Exporter",
      onClick: ({ clearSelection }) => clearSelection(),
    },
  ];
  const rowActions = [];

  return (
    <Page style={{height: "80vh"}}>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "space-between",
          marginLeft: "auto",
        }}
      >
        <div style={{ flex: 1, marginBottom: 0, marginTop: 0 }}>
          <h2>Etat Des Affectations</h2>
        </div>
        <div style={{ display: "flex" }}>
          <DatePicker
            value={dateRange}
            onChange={({ date }) => {
              setDateRange(Array.isArray(date) ? date : [date]);
              console.log(date);
            }}
            formatString="dd/MM/yyyy"
            name="dateRange"
            mask={null}
            locale={fr}
            size="compact"
            range
          />

          <Button
            onClick={fetchAffectation}
            size={SIZE.compact}
            shape={SHAPE.pill}
            style={{ marginLeft: 10 }}
          >
            Rechercher
          </Button>
        </div>
      </div>
      <hr />
      {loading ? (
        <Loading />
      ) : (
        <StatefulDataTable
          batchActions={batchActions}
          rowActions={rowActions}
          columns={columns}
          rows={rows}
        />
      )}
    </Page>
  );
}
