import React from "react";
import { styled } from "baseui";
import { StatefulInput } from "baseui/input";
import { Switch, Route } from "react-router-dom";
import SideNavigation from "./SideNavigation";
import TopBar from "./TopBar";

import Home from "./Home";
import Users from "../routes/Users";
import Cars from "../routes/Cars";
import EMR from "../routes/EMR";
import Drivers from "../routes/Drivers";
import Clients from "../routes/Clients";
import Affectations from "../routes/Affectations";
import Decharges from "../routes/Decharges";
import GACheckList from "../routes/GACheckList";
import Notifications from "../routes/Notifications";
import EtatAffectations from "../routes/EtatAffectations"
import CarsState from "../routes/CarsState";
import Settings from "../routes/Settings";

const MainContainer = styled("div", {
  display: "flex",
  flex: 1,
});

const Container = styled("div", {
  display: "flex",
  flex: 1,
});

export default function App() {
  return (
    <MainContainer>
      <TopBar />
      <Container>
        <SideNavigation />
        <Switch>
        <Route path="/users">
            <Users />
          </Route>
          <Route path="/settings">
            <Settings />
          </Route>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/notifications">
            <Notifications />
          </Route>
          <Route path="/carclients/:id/ga_checklist">
            <GACheckList />
          </Route>
          <Route path="/cars">
            <Cars />
          </Route>
         <Route path="/cars_state">
            <CarsState />
          </Route>
          <Route path="/drivers">
            <Drivers />
          </Route>
          <Route path="/clients">
            <Clients />
          </Route>
          <Route path="/affectations">
            <Affectations />
          </Route>
          <Route path="/decharges">
            <Decharges />
          </Route>
          <Route path="/etat_affectations">
            <EtatAffectations />
          </Route>
          <Route path="/emr">
            <EMR />
          </Route>
        </Switch>
      </Container>
    </MainContainer>
  );
}
