import React, { useEffect, useState } from "react";
import { Switch, Route } from "react-router-dom";
import { FormControl } from "baseui/form-control";
import { Input } from "baseui/input";
import { Select, TYPE } from "baseui/select";

import { Button, SIZE, SHAPE } from "baseui/button";
import { useHistory, useRouteMatch } from "react-router-dom";
import {
  FaEdit,
  FaEye,
  FaTrash,
  FaHistory,
  FaTasks,
  FaBackspace,
  FaExternalLinkAlt,
  FaCartPlus,
  FaLevelDownAlt,
  FaLevelUpAlt,
  FaPeace,
  FaUserTie,
  FaClipboardList
} from "react-icons/fa";

import { useSnackbar } from "baseui/snackbar";
import {
  Checkbox,
  StatefulCheckbox,
  STYLE_TYPE,
  LABEL_PLACEMENT,
} from "baseui/checkbox";

import { useStyletron } from "baseui";
import Alert from "baseui/icon/alert.js";
import Check from "baseui/icon/check.js";

import DeleteAlt from "baseui/icon/delete-alt";

import {
  StatefulDataTable,
  BooleanColumn,
  CategoricalColumn,
  NumericalColumn,
  StringColumn,
  NUMERICAL_FORMATS,
} from "baseui/data-table";

import { useForm, Controller } from "react-hook-form";

import { useAppStore } from "../stores";

import Page from "../components/Page";
import BackButton from "../components/BackButton";
import Loading from "../components/Loading";

export default function Cars() {
  return (
    <Switch>
      <Route path="/emr" exact>
        <All />
      </Route>
    </Switch>
  );
}

const columns = [
  StringColumn({
    title: "Matricule",
    mapDataToValue: (data) => data["matricule"],
  }),

  StringColumn({
    title: "Code GPS",
    mapDataToValue: (data) => data["code_gps"],
  }),
  CategoricalColumn({
    title: "Marque",
    mapDataToValue: (data) => data["marque"],
  }),
  CategoricalColumn({
    title: "Genre",
    mapDataToValue: (data) => data["genre"],
  }),
  CategoricalColumn({
    title: "Status",
    mapDataToValue: (data) => {
      let state = data.latestState?.name ? data["latestState"]["name"] : "En marche"
      if (state === "marche") state = "En marche"
      if (state === "entre_atelie") state = "Entre d'atelie"
      if (state === "sortie_atelie") state = "Sortie d'atelie"
      if (state === "accedente") state = "Vhicule Accedent"
      return state
    }
  }),

  CategoricalColumn({
    title: "Client",
    mapDataToValue: (data) =>
      data["latestClient"]
        ? data["latestClient"]["designation"]
        : "Non Affect",
  }),
  CategoricalColumn({
    title: "Conducteur",
    mapDataToValue: (data) =>
      data["latestDriver"]
        ? data["latestDriver"]["firstname"] + " " + data["latestDriver"]["lastname"]
        : "Sans Conducteur",
  })
];

export function All() {
  const [rows, setRows] = React.useState([]);
  const [loading, setLoading] = useState(true);
  const history = useHistory();
  const { api, user } = useAppStore();
  const { enqueue } = useSnackbar();

  useEffect(() => {
    api
      .get("/cars")
      .then((res) => {
        setRows(res?.data?.data?.map((r) => ({ id: r.id, data: r })));
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  }, []);

  const batchActions = [
    {
      label: "Exporter",
      onClick: ({ clearSelection }) => clearSelection(),
    },
  ];
  const rowActions = [];

  rowActions.push({
    label: "Historique des status",
    onClick: ({ row }) => history.push("/cars_state/" + row.id),
    renderIcon: ({ size }) => <FaClipboardList size={size} />,
  });

  rowActions.push({
    label: "Historique des conducteur",
    onClick: ({ row }) => history.push("/cars/drivers/" + row.id),
    renderIcon: ({ size }) => <FaPeace size={size} />,
  });
  rowActions.push({
    label: "Historique des clients",
    onClick: ({ row }) => history.push("/cars/clients/" + row.id),
    renderIcon: ({ size }) => <FaUserTie size={size} />,
  });
  rowActions.push({
    label: "Affiche",
    onClick: ({ row }) => history.push("/cars/show/" + row.id),
    renderIcon: ({ size }) => <FaEye size={size} />,
  });

  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }
  return (
    <Page style={{ height: "80vh" }}>
      <StatefulDataTable
        batchActions={batchActions}
        rowActions={rowActions}
        columns={columns}
        rows={rows}
      />
    </Page>
  );
}

