export default function Cars() {
  return (
    <Switch>
      <Route path="/cars" exact>
        <All />
      </Route>
      <Route path="/cars/add">
        <Add />
      </Route>
      <Route path="/cars/edit/:id">
        <Edit />
      </Route>
      <Route path="/cars/show/:id">
        <Show />
      </Route>
      <Route path="/cars/clients/:id">
        <CarClients />
      </Route>
      <Route path="/cars/drivers/:id">
        <CarDrivers />
      </Route>
    </Switch>
  );
}

const columns = [
  StringColumn({
    title: "Matricule",
    mapDataToValue: (data) => data["matricule"],
  }),
  StringColumn({
    title: "Ancien Matricule",
    mapDataToValue: (data) => data["old_matricule"],
  }),
  StringColumn({
    title: "Code GPS",
    mapDataToValue: (data) => data["code_gps"],
  }),
  StringColumn({
    title: "Genre",
    mapDataToValue: (data) => data["genre"],
  }),
  StringColumn({
    title: "Genre",
    mapDataToValue: (data) => data["genre"],
  }),
  StringColumn({
    title: "Puissance",
    mapDataToValue: (data) => data["puissance"],
  }),
  StringColumn({
    title: "Energie",
    mapDataToValue: (data) => data["energie"],
  }),
  StringColumn({
    title: "Carrosserie",
    mapDataToValue: (data) => data["carrosserie"],
  }),
];

export function All() {
  const [rows, setRows] = React.useState([]);
  const [loading, setLoading] = useState(true);
  const history = useHistory();
  const { api } = useAppStore();
  const { enqueue } = useSnackbar();

  useEffect(() => {
    api
      .get("/cars")
      .then((res) => {
        setRows(res?.data?.data?.map((r) => ({ id: r.id, data: r })));
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  }, []);

  function removeRows(ids) {
    const nextRows = rows.filter((row) => !ids.includes(row.id));
    setRows(nextRows);
  }
  function removeRow(id) {
    removeRows([id]);
  }
  const batchActions = [
    {
      label: "Exporter",
      onClick: ({ clearSelection }) => clearSelection(),
    },
  ];
  const rowActions = [
    {
      label: "Affiche",
      onClick: ({ row }) => history.push("/cars/edit/" + row.id),
      renderIcon: ({ size }) => <FaEdit size={size} />,
    },
    {
      label: "Supprimer",
      onClick: ({ row }) => {
        if (window.confirm("Supprimer ?")) {
          api
            .delete("/cars/" + row.id)
            .then((res) => {
              removeRow(row.id);
              enqueue({ message: "Success" });
            })
            .catch(() => {
              enqueue({ message: "Error" });
            });
        }
      },
      renderIcon: ({ size }) => <FaTrash size={size} />,
    },
  ];
  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }
  return (
    <Page style={{height: "75vh"}}>
      <Button
        onClick={() => history.push("/cars/add")}
        size={SIZE.compact}
        shape={SHAPE.pill}
      >
        Ajouter une vhicule
      </Button>
      <hr />
      <StatefulDataTable
        batchActions={batchActions}
        rowActions={rowActions}
        columns={columns}
        rows={rows}
      />
    </Page>
  );
}

