import React, { useEffect, useState } from "react";
import { BrowserRouter } from "react-router-dom";
import { Spinner } from "baseui/spinner";
import { ToastContainer } from "react-toastify";

import useApi from "./hooks/useApi";

import Main from "./containers/Main";
import Login from "./containers/Login";

import { AppProvider } from "./stores";
import ScrollToTopOnLocationChange from "./components/ScrollToTopOnLocationChange";

const AppContainer = ({ children }) => (
  <BrowserRouter>
    <ScrollToTopOnLocationChange>{children}</ScrollToTopOnLocationChange>
    <ToastContainer
      position="top-center"
      autoClose={3000}
      newestOnTop={false}
      hideProgressBar
      rtl={false}
      pauseOnFocusLoss
      draggable
      pauseOnHover
    />
  </BrowserRouter>
);
export default function App() {
  const [isLoadingUser, setIsLoadingUser] = useState(true);
  const [user, setUser] = useState(null);
  const { api, setToken } = useApi();
  useEffect(() => {
    let isSubscribed = true;
    setIsLoadingUser(true);
    api
      .get("/auth/me")
      .then(({ data }) => {
        if (isSubscribed) {
          setUser(data.data);
          setIsLoadingUser(false);
        }
      })
      .catch(() => {
        if (isSubscribed) {
          setUser(null);
          setIsLoadingUser(false);
        }
      });
    return () => (isSubscribed = false);
  }, []);

  if (isLoadingUser) {
    return (
      <div
        style={{
          flex: 1,
          height: "80vh",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Spinner />
      </div>
    );
  }

  return (
    <AppContainer>
      <AppProvider value={{ user, setUser, api, setToken }}>
        {user ? <Main /> : <Login />}
      </AppProvider>
    </AppContainer>
  );
}
