import React, { useEffect, useState } from "react";
import { Switch, Route } from "react-router-dom";
import { FormControl } from "baseui/form-control";
import { Input } from "baseui/input";
import { Button, SIZE, SHAPE } from "baseui/button";
import { useHistory, useRouteMatch } from "react-router-dom";
import { FaEdit, FaEye, FaTrash } from "react-icons/fa";
import { toast } from "react-toastify";
import { useSnackbar } from "baseui/snackbar";
import {
  Checkbox,
  StatefulCheckbox,
  STYLE_TYPE,
  LABEL_PLACEMENT,
} from "baseui/checkbox";

import { useStyletron } from "baseui";
import Alert from "baseui/icon/alert.js";
import Check from "baseui/icon/check.js";

import DeleteAlt from "baseui/icon/delete-alt";
import Show from "baseui/icon/show";

import {
  StatefulDataTable,
  BooleanColumn,
  CategoricalColumn,
  NumericalColumn,
  StringColumn,
  NUMERICAL_FORMATS,
} from "baseui/data-table";

import { useForm, Controller } from "react-hook-form";

import { useAppStore } from "../stores";

import Page from "../components/Page";
import BackButton from "../components/BackButton";
import Loading from "../components/Loading";

export default function Cars() {
  return (
    <Switch>
      <Route path="/drivers" exact>
        <All />
      </Route>
      <Route path="/drivers/add">
        <Add />
      </Route>
      <Route path="/drivers/edit/:id">
        <Edit />
      </Route>
    </Switch>
  );
}

const columns = [
  StringColumn({
    title: "Nom",
    mapDataToValue: (data) => data["firstname"],
  }),
  StringColumn({
    title: "Prnom",
    mapDataToValue: (data) => data["lastname"],
  }),
  StringColumn({
    title: "Tel",
    mapDataToValue: (data) => data["tel"],
  }),
  StringColumn({
    title: "Code paie",
    mapDataToValue: (data) => data["code_paie"],
  }),
];

export function All() {
  const [rows, setRows] = React.useState([]);
  const [loading, setLoading] = useState(true);
  const history = useHistory();
  const { api } = useAppStore();
  const { enqueue } = useSnackbar();

  useEffect(() => {
    api
      .get("/drivers")
      .then((res) => {
        setRows(res?.data?.data?.map((r) => ({ id: r.id, data: r })));
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
      });
  }, []);

  function removeRows(ids) {
    const nextRows = rows.filter((row) => !ids.includes(row.id));
    setRows(nextRows);
  }
  function removeRow(id) {
    removeRows([id]);
  }
  const batchActions = [
    {
      label: "Exporter",
      onClick: ({ clearSelection }) => clearSelection(),
    },
  ];
  const rowActions = [
    {
      label: "Affiche",
      onClick: ({ row }) => history.push("/drivers/edit/" + row.id),
      renderIcon: ({ size }) => <FaEdit size={size} />,
    },
    {
      label: "Supprimer",
      onClick: ({ row }) => {
        if (window.confirm("Supprimer ?")) {
          api
            .delete("/drivers/" + row.id)
            .then((res) => {
              removeRow(row.id);
              enqueue({ message: "Success" });
            })
            .catch(() => {
              enqueue({ message: "Error" });
            });
        }
      },
      renderIcon: ({ size }) => <FaTrash size={size} />,
    },
  ];
  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }
  return (
    <Page style={{height: "75vh"}}>
      <Button
        onClick={() => history.push("/drivers/add")}
        size={SIZE.compact}
        shape={SHAPE.pill}
      >
        Ajouter un conducteur
      </Button>
      <hr />
      <StatefulDataTable
        batchActions={batchActions}
        rowActions={rowActions}
        columns={columns}
        rows={rows}
      />
    </Page>
  );
}

function Add() {
  const [roles, setRoles] = useState([]);
  const history = useHistory();
  const [checkedUserRoles, setCheckedUserRoles] = React.useState([]);
  const [loading, setLoading] = useState(false);
  const { control, handleSubmit, errors } = useForm();
  const { api } = useAppStore();
  const { enqueue } = useSnackbar();

  const onSubmit = async (data) => {
    try {
      const res = await api.post("/drivers", data);
      toast.dark("Bien ajouter.")
      history.push("/drivers");
    } catch (error) {
      enqueue({ message: "Error !" });
    }
  };

  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }

  return (
    <Page>
      <BackButton title="Ajouter un conducteur" />
      <form style={{ width: 500 }} onSubmit={handleSubmit(onSubmit)}>
        <FormControl label="Nom">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="firstname"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Prnom">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="lastname"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="TEL">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="tel"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Code Paie">
          <Controller
            control={control}
            defaultValue=""
            as={Input}
            required
            name="code_paie"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <Button
          style={{ width: "100%" }}
          type="submit"
          size={SIZE.compact}
          shape={SHAPE.pill}
        >
          Enregistrer
        </Button>
      </form>
    </Page>
  );
}

function Edit() {
  const [loading, setLoading] = useState(true);
  const { enqueue } = useSnackbar();
  const { params } = useRouteMatch();
  const { api } = useAppStore();
  const [selected, setSelected] = useState(null);
  const history = useHistory();
  const hookForm = useForm();
  const { control, handleSubmit, errors } = hookForm;

  const onSubmit = async (data) => {
    const res = await api.put("/drivers/" + params.id, data);
    if (res) {
      enqueue({ message: "Success" });
      history.push("/drivers");
    } else {
      enqueue({ message: "Error !" });
    }
  };

  useEffect(async () => {
    const res = await api.get("/drivers/" + params.id);
    if (res) {
      setSelected(res.data.data);
    } else {
    }
    setLoading(false);
  }, []);

  if (loading) {
    return (
      <Page>
        <Loading />
      </Page>
    );
  }

  return (
    <Page>
      <BackButton
        title={` Modifier le conducteur: ${
          selected?.firstname + " " + selected?.lastname
        }`}
      />
      <form style={{ width: 500 }} onSubmit={handleSubmit(onSubmit)}>
        <FormControl label="Nom">
          <Controller
            control={control}
            defaultValue={selected.firstname}
            as={Input}
            required
            name="firstname"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Prnom">
          <Controller
            control={control}
            defaultValue={selected.lastname}
            as={Input}
            required
            name="lastname"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="TEL">
          <Controller
            control={control}
            defaultValue={selected.tel}
            as={Input}
            required
            name="tel"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <FormControl label="Code Paie">
          <Controller
            control={control}
            defaultValue={selected.code_paie}
            as={Input}
            required
            name="code_paie"
            size="compact"
            placeholder=""
          />
        </FormControl>
        <Button
          style={{ width: "100%" }}
          size={SIZE.compact}
          shape={SHAPE.pill}
          type="submit"
        >
          Enregistrer
        </Button>
      </form>
    </Page>
  );
}
